import { Component, OnInit } from '@angular/core';
import { SeoService } from '@core/services/seo.service';
import { environment } from '@environments/environment';

@Component({
    selector: 'findqo-place-ad-guide',
    templateUrl: './place-ad-guide.component.html',
    styleUrl: './place-ad-guide.component.scss',
    standalone: false
})
export class PlaceAdGuideComponent implements OnInit {
	constructor(private readonly seoService: SeoService) {}

	readonly appUrl = environment.appUrl;
	readonly tenantInUrl = environment.tenantIn;

	ngOnInit(): void {
		this.seoService.setSeo({
			title: 'Advertise Property in Ireland',
			description:
				'FindQo - website & mobile app to sell/rent real estate properties in Ireland. Post ads Free and get Unlimited Enquiries. Advertise to rent or sell and save.',
		});
	}
}
